.profile{
  &-info{
    &__wrapper{
      padding: 25px 17px;

      max-width: 370px;
      width: 100%;
      height: 125px;

      background: #EBEFF6;
      border-radius: 15px;

      display: flex;
      align-items: center;
    }
    &__img{
      width: 75px;
      height: 75px;

      flex-shrink: 0;
      &-item {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
      }
    }
    &__name{
      margin-left: 15px;

      font-weight: bold;
      font-size: 20px;
      line-height: 25px;

      color: #071013;

      word-break: break-all;
    }
  }
  &-content{
    &__tabs{
      margin-top: 60px;
    }
  }
  &-tabs{
    &__nav{
      display: flex;
      align-items: center;
      &-item + &-item{
        margin-left: 40px;
      }
      &-item{
        &.active{
          .profile-tabs__nav-link{
              color:#232B3F;

              border-bottom: 2px solid $accentColor;
          }
        }
      }
      &-link{
        font-weight: bold;
        font-size: 23px;
        line-height: 28px;

        color: #8A8A95;
      }
    }
    &__content{
      margin-top: 40px;
    }
  }
  &-form{
    &__input{
      max-width: 470px;
    }
    &__input + &__input{
      margin-top: 30px;
    }
    &__btn{
      max-width: 195px;
      height: 42px;
    }
  }
}

.buy-grid{
  &__row{
    display: grid;
    grid-template-columns: 130px auto 170px 170px;
    grid-auto-rows: 60px;
  }
  &__item{
    display: flex;
    align-items: center;
    &_end{
      justify-content: flex-end;
    }
  }
  &__date{
    
  }
}

@media screen and (max-width: 600px) {
  .profile{
    &-tabs{
      &__nav{
        flex-direction: column;
        &-item + &-item{
          margin-left: 0;

          margin-top: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .form{
    &__input{
        &-img{
          flex-direction: column;
          &-block + &-block{
            margin-left: 0;
            margin-top: 10px;
          }
      }
    }
  }
}