.card{
  &-grid{
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    column-gap: 30px;
    row-gap: 20px;
  }
  &-item{
    background: #EBEFF6;
    border-radius: 15px;

    overflow: hidden;
    &__img{
      width: 100%;
      height: 250px;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    &__wrapper{
      margin-top: 10px;
      padding: 20px;
    }
    &__title{
      font-weight: 700;
      font-size: 25px;
      line-height: 110%;

      letter-spacing: -0.02em;

      color: #232B3F;
    }
    &__desc{
      font-weight: 500;
      font-size: 18px;
      line-height: 136%;

      color: #232B3F;

      margin-top: 10px;
    }
    &__category{
      margin: -5px;
      margin-top: 5px;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      &-item{
        margin: 5px;

        background: rgba(35, 43, 63, 0.1);
        border-radius: 15px;

        padding: 5px 10px;

        height: 32px;

        display: flex;
        align-items: center;
        justify-content: center;

        width: max-content;
      }
      &-label{
        font-weight: 600;
        font-size: 18px;

        display: flex;
        align-items: flex-end;
        text-align: center;


        color: #232B3F;
      }
    }
    &__footer{
      margin-top: 20px;
    }
    &__btn{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 130px;
      height: 40px;

      background: #E5134A;
      border-radius: 4px;

      font-weight: 700;
      font-size: 18px;
      text-align: center;

      color: #FFFFFF;
    }
  }
}

.category{
  &-wrapper{
    &__title{
      font-weight: bold;
      font-size: 23px;
      color: #232B3F;
    }
    &__content{
      margin-top: 5px;
    }
  }
  &-list{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: -5px;
    &__item{
      margin: 5px;
    }
    &__link{
      font-weight: 600;
      font-size: 18px;

      display: flex;
      align-items: center;
      text-align: center;


      color: #232B3F;

      background: rgba(35, 43, 63, 0.1);
      border-radius: 15px;

      padding: 5px 10px;

      height: 32px;

      width: max-content;
    }
  }
}

@media screen and (max-width: 767px) {
  .card{
    &-grid{
      grid-template-columns: minmax(0,1fr);
    }
  }
}