body{
  font-family: 'Gilroy', sans-serif;
  font-weight: 400;
  &.no-scroll{
    overflow: hidden;
  }
}

.page-wrapper{
  display: flex;
  flex-direction: column;

  min-height: 100vh;
  height: 100%;
}

.page-search{
  width: 100%;
  height: 50px;
  background: #F1F0F2;
  border-radius: 15px;
  &__input{
    -webkit-appearance: none;
    background: none;
    border:none;
    width: 100%;
    height:100%;
    padding: 15px 25px;
    border-radius: 15px;

    font-size: 18px;
    font-weight: 600;
    color:#071013;
    &::placeholder{
      color:rgba(#071013,.5);
    }
  }
}

.content{
  width: 100%;
  height: 100%;

  flex: 1 0 auto;

  padding-top: 40px;

  &-login{
    min-height: 970px;
    padding: 145px 0;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-page{
    padding: 40px 0 155px 0;
    &__header + &__content{
      margin-top: 40px;
    }
    &__title{
      font-weight: 700;
      font-size: 30px;
      color: #232B3F;
    }
    &__title + &__wrapper{
      margin-top: 20px;
    }
    &__search{
      margin-top: 20px;
      max-width: 770px;
    }
    &__grid{
      display: grid;
      grid-template-columns: 770px auto;
      gap: 30px;

      margin-top: 20px;
    }
  }
}

.container{
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;

  margin: 0 auto;
}

.trigger{
  &-link{
    &:hover{
      cursor: pointer;
    }
  }
  &-content{
    opacity: 0;
    z-index: -1;

    transition: 0.3s all;
    &.active{
      opacity: 1;
      z-index: 9;
    }
  }
}

.tabs{
  &-content{
    &__item{
      display: none;
      &.active{
        display: block;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .content{
    &-login{
      min-height: auto;
    }
    &-page{
      &__grid{
        grid-template-columns: minmax(0,1fr);
        &-item{
          &:nth-child(2){
            order: 1;
          }
          &:nth-child(1){
            order: 2;
          }
        }
      }
      &__search{
        max-width: 100%;
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .logo{
    max-width: 150px;
  }
}


