.popup{
  position: fixed;
  top: 0;
  left: 0;

  height: 100%;
  width: 100%;

  background: rgba(35, 43, 63, 0.5);
  backdrop-filter: blur(15px);


  opacity: 0;
  z-index: -1;

  transition: .5s all;

  &.active{
    opacity: 1;
    z-index: 99;
  }
  &__wrapper{
    height: 100%;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 25px;
  }
  &__content{
    max-width: 460px;
    width: 100%;
    max-height: 100%;

    overflow-y:auto;
  }
  &-trigger{
    &:hover{
      cursor: pointer;
    }
  }
}