.page{
  &-banner{
    &__wrapper{
      display: flex;
      align-items: center;

      padding-right: 90px;

      background-color: #EBEFF6;
      border-radius: 15px;

      overflow: hidden;
      &_padding{
        padding-left: 15px;
      }
    }
    &__img{
      height: 175px;
      width: 140px;

      flex-shrink: 0;

      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__content{
      margin-left: 50px;
    }
    &__title{
      font-weight: 700;
      font-size: 30px;
      letter-spacing: -0.02em;
      color: #232B3F;
    }
    &__desc{
      margin-top: 10px;

      font-weight: 500;
      font-size: 18px;
      line-height: 136%;
      color: #232B3F;
    }
    &__arrow{
      margin-left: auto;
    }
    &__btn{
      display: block;
      width: 30px;
      height: 30px;
      flex-shrink: 0;

      svg{
        width: 100%;
        height: 100%;
      }
    }
    &__link{
      display: none;
      align-items: center;
      justify-content: center;

      width: 130px;
      height: 45px;

      background: #E5134A;
      border-radius: 4px;

      font-weight: bold;
      font-size: 18px;
      color: #FFFFFF;
    }
  }
}

@media screen and (max-width: 767px) {
  .page{
    &-banner{
      &__wrapper{
        flex-direction: column;

        padding: 0 0 30px 0;
      }
      &__img{
        width: 100%;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        &_not-full{
          padding-top: 25px;
          img{
            object-fit: contain;
          }
        }
      }
      &__content{
        margin-left: 0;
        margin-top: 30px;

        padding: 0 30px;
      }
      &__arrow{
        margin-top: 10px;
        padding: 0 30px;

        margin-left: 0;

        width: 100%;
      }
      &__btn{
        display: none;
      }
      &__link{
        display: flex;
      }
    }
  }
}