.nav{
  &__list{
    display: flex;
    align-items: center;
  }
  &-list{
    &__item + &__item{
      margin-left: 30px;
    }
  }
  &-item{
    &__link{
      font-weight: bold;
      font-size: 18px;
      color: #FFFFFF;

      transition: 0.3s color;
      &:hover{
        color:$accentColor;
      }
    }
  }
}