  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Gilroy', sans-serif;
  font-weight: 400;
}

body.no-scroll {
  overflow: hidden;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
}

.page-search {
  width: 100%;
  height: 50px;
  background: #F1F0F2;
  border-radius: 15px;
}

.page-search__input {
  -webkit-appearance: none;
  background: none;
  border: none;
  width: 100%;
  height: 100%;
  padding: 15px 25px;
  border-radius: 15px;
  font-size: 18px;
  font-weight: 600;
  color: #071013;
}

.page-search__input::placeholder {
  color: rgba(7, 16, 19, 0.5);
}

.content {
  width: 100%;
  height: 100%;
  flex: 1 0 auto;
  padding-top: 40px;
}

.content-login {
  min-height: 970px;
  padding: 145px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-page {
  padding: 40px 0 155px 0;
}

.content-page__header + .content-page__content {
  margin-top: 40px;
}

.content-page__title {
  font-weight: 700;
  font-size: 30px;
  color: #232B3F;
}

.content-page__title + .content-page__wrapper {
  margin-top: 20px;
}

.content-page__search {
  margin-top: 20px;
  max-width: 770px;
}

.content-page__grid {
  display: grid;
  grid-template-columns: 770px auto;
  gap: 30px;
  margin-top: 20px;
}

.container {
  max-width: 1220px;
  padding: 0 25px;
  width: 100%;
  margin: 0 auto;
}

.trigger-link:hover {
  cursor: pointer;
}

.trigger-content {
  opacity: 0;
  z-index: -1;
  transition: 0.3s all;
}

.trigger-content.active {
  opacity: 1;
  z-index: 9;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item.active {
  display: block;
}

@media screen and (max-width: 1100px) {
  .content-login {
    min-height: auto;
  }
  .content-page__grid {
    grid-template-columns: minmax(0, 1fr);
  }
  .content-page__grid-item:nth-child(2) {
    order: 1;
  }
  .content-page__grid-item:nth-child(1) {
    order: 2;
  }
  .content-page__search {
    max-width: 100%;
  }
}

@media screen and (max-width: 550px) {
