.form-checkbox {
  display: flex;
  align-items: flex-start;
}

.form-checkbox:hover {
  cursor: pointer;
}

.form-checkbox__item {
  display: none;
}

.form-checkbox__item:checked ~ .form-checkbox__checkmark::before {
  opacity: 1;
}

.form-checkbox__checkmark {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  width: 30px;
  height: 30px;
  flex-shrink: 0;
  border: 1.71429px solid #232B3F;
  box-sizing: border-box;
  border-radius: 3.42857px;
  position: relative;
}

.form-checkbox__checkmark::before {
  content: '';
  position: absolute;
  top: calc(50% - 6px);
  left: calc(50% - 9px);
  width: 18px;
  height: 12px;
  display: block;
  background: url("../img/check.svg") center center/cover no-repeat;
  opacity: 0;
  transition: .3s opacity;
}

.form-checkbox__label {
  font-size: 16px;
  line-height: 20px;
