  padding: 20px;
}

.card-item__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 110%;
  letter-spacing: -0.02em;
  color: #232B3F;
}

.card-item__desc {
  font-weight: 500;
  font-size: 18px;
  line-height: 136%;
  color: #232B3F;
  margin-top: 10px;
}

.card-item__category {
  margin: -5px;
  margin-top: 5px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.card-item__category-item {
  margin: 5px;
  background: rgba(35, 43, 63, 0.1);
  border-radius: 15px;
  padding: 5px 10px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
}

.card-item__category-label {
  font-weight: 600;
  font-size: 18px;
  display: flex;
  align-items: flex-end;
  text-align: center;
  color: #232B3F;
}

.card-item__footer {
  margin-top: 20px;
}

.card-item__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 130px;
  height: 40px;
  background: #E5134A;
  border-radius: 4px;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  color: #FFFFFF;
}

.category-wrapper__title {
  font-weight: bold;
  font-size: 23px;
  color: #232B3F;
}

.category-wrapper__content {
  margin-top: 5px;
}

.category-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: -5px;
}

.category-list__item {
  margin: 5px;
}

.category-list__link {
  font-weight: 600;
  font-size: 18px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #232B3F;
  background: rgba(35, 43, 63, 0.1);
  border-radius: 15px;
  padding: 5px 10px;
  height: 32px;
  width: max-content;
}

@media screen and (max-width: 767px) {
  .card-grid {
    grid-template-columns: minmax(0, 1fr);
  }
}

.profile-info__wrapper {
  padding: 25px 17px;
  max-width: 370px;
  width: 100%;
  height: 125px;
  background: #EBEFF6;
  border-radius: 15px;
  display: flex;
  align-items: center;
}

.profile-info__img {
  width: 75px;
  height: 75px;
  flex-shrink: 0;
}

.profile-info__img-item {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.profile-info__name {
  margin-left: 15px;
  font-weight: bold;
  font-size: 20px;
  line-height: 25px;
  color: #071013;
  word-break: break-all;
}

.profile-content__tabs {
  margin-top: 60px;
}

.profile-tabs__nav {
  display: flex;
  align-items: center;
}

.profile-tabs__nav-item + .profile-tabs__nav-item {
  margin-left: 40px;
}

.profile-tabs__nav-item.active .profile-tabs__nav-link {
  color: #232B3F;
  border-bottom: 2px solid #E5134A;
}

.profile-tabs__nav-link {
  font-weight: bold;
  font-size: 23px;
  line-height: 28px;
  color: #8A8A95;
}

.profile-tabs__content {
  margin-top: 40px;
}

.profile-form__input {
  max-width: 470px;
}

.profile-form__input + .profile-form__input {
  margin-top: 30px;
}

.profile-form__btn {
  max-width: 195px;
  height: 42px;
}

.buy-grid__row {
  display: grid;
  grid-template-columns: 130px auto 170px 170px;
  grid-auto-rows: 60px;
}

.buy-grid__item {
  display: flex;
  align-items: center;
}

.buy-grid__item_end {
  justify-content: flex-end;
}

@media screen and (max-width: 600px) {
  .profile-tabs__nav {
    flex-direction: column;
  }
  .profile-tabs__nav-item + .profile-tabs__nav-item {
    margin-left: 0;
    margin-top: 15px;
  }
}

@media screen and (max-width: 375px) {
  .form__input-img {
    flex-direction: column;
  }
  .form__input-img-block + .form__input-img-block {
    margin-left: 0;
    margin-top: 10px;
  }
}
