.footer{
  flex-shrink: 0;
  &__wrapper{
    padding: 40px 0 25px 0;
    background: #232B3F;
  }
  &__row{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__social{
    max-width: 340px;
    width: 100%;
  }
  &__block{
    &-title{
      font-size: 16px;
      font-weight: 500;
      color:#616F93;
    }
    &-header + &-content{
      margin-top: 15px;
    }
    &-link{
      font-weight: 500;
      font-size: 16px;
      color: #FFFFFF;
    }
  }
  &__row + &__row{
    margin-top: 50px;
  }
}

.social-list{
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;
  &__item{
    flex-shrink: 0;
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 32px;
    height: 32px;

    border-radius: 50%;

    background-color: #1B2233;

    transition: .3s background-color linear;
    &:hover{
      background-color: rgba(#1B2233,.6);
    }

    svg{
      width: 16px;
      height: 16px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .footer{
    &__block{
      &_nav{
        display: none;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .footer{
    &__row{
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &__row + &__row{
      margin-top: 30px;
    }
    &__block{
      &_social{
        margin-top: 15px;
      }
      &-title{
        text-align: center;
      }
    }
    &__block + &__block{
      margin-top: 10px;
    }
  }
}