.form{
  &__header{
    display: flex;
    justify-content: space-between;
    &-link{
      display: flex;
      align-items: center;
      &__text{
        font-weight: 600;
        font-size: 18px;
        color: #8A8A95;
      }
      &__icon{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;

        width: 12px;
        height: 12px;

        margin-left: 10px;
        svg{
          width: 100%;
          height: 100%;
        }
      }
    }

  }
  &__wrapper{
    max-width: 470px;
    width: 100%;

    padding: 40px;

    background: #EBEFF6;
    border-radius: 20px;
  }
  &__title{
    font-size: 30px;
    line-height: 37px;

    color: #000000;
  }
  &__desc{
    margin-top: 24px;
  }
  &__text{
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #8A8A95;
    &-dark{
      color: rgb(35,43,63);
    }
    &-accent{
      color:$accentColor;
    }
  }
  &__text + &__text{
    margin-top: 20px;
  }
  &__content{
    margin-top: 30px;
  }
  &__input + &__input{
    margin-top: 16px;
  }
  &__btn{
    -webkit-appearance: none;
    border: none;
    outline: none;

    width: 100%;
    height: 60px;

    background: $accentColor;
    border-radius: 4px;

    font-size: 18px;
    font-weight: 600;
    color: #FFFFFF;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-top: 24px;
    &:hover{
      cursor: pointer;
    }
  }
  &__input{
    &-label{
      font-weight: 500;
      font-size: 18px;
      line-height: 136%;
      color: #232B3F;
      margin-bottom: 18px;
    }
    &-item{
      -webkit-appearance: none;

      outline: none;

      width: 100%;
      padding: 20px 15px;

      font-size: 18px;
      font-weight: 600;

      color: rgb(35,43,63);


      background: #FFFFFF;
      border: 1px solid #CCD7EA;
      border-radius: 4px;

      height: 60px;
      &::placeholder{
        color: #8A8A95;
      }
    }
    &-img{
      display: flex;
      align-items: center;
      &-block + &-block{
        margin-left: 20px;
      }
      &-input{
        input{
          display: none;
        }
      }
      &-btn{
        width: 160px;
        height: 40px;

        margin-top: 0;
      }
      &-label{
        font-weight: 500;
        font-size: 18px;
        color: #8A8A95;
      }
      &-item{
        width: 64px;
        height: 64px;
        border-radius: 50%;
        object-fit: cover;
      }
    }
    &-password{
      position: relative;
      &-icon{
        position: absolute;
        right: 17px;
        top:calc(50% + 8px);

        display: block;
        width: 24px;
        height: 24px;

        svg{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  &__footer{
    margin-top: 28px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &-text{
      text-align: center;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: #8A8A95;
    }
    &-block{
      & + &_social{
        margin-top: 15px;
      }
      &_social + &{
        margin-top: 25px;
      }
    }
  }
  &__social{
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 185px;
  }
  &__checkbox{
    margin-top: 24px;
  }
  &-checkbox{
    display: flex;
    align-items: flex-start;
    &:hover{
      cursor: pointer;
    }
    &__item{
      display: none;
      &:checked ~ .form-checkbox__checkmark{
        &::before{
          opacity: 1;
        }
      }
    }
    &__checkmark{
      display: flex;
      align-items: center;
      justify-content: center;

      margin-right: 20px;

      width: 30px;
      height: 30px;
      flex-shrink: 0;

      border: 1.71429px solid #232B3F;
      box-sizing: border-box;
      border-radius: 3.42857px;

      position: relative;

      &::before{
        content: '';

        position: absolute;
        top:calc(50% - 6px);
        left: calc(50% - 9px);

        width: 18px;
        height: 12px;
        display: block;

        background:url("../img/check.svg") center center/cover no-repeat;

        opacity: 0;

        transition: .3s opacity;
      }
    }
    &__label{
      font-size: 16px;
      line-height: 20px;
    }
  }
  &-social{
    &__item{
      &-icon{
        display: block;
        width: 50px;
        height: 50px;
        svg{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .form{
    &__wrapper{
      &_small{
        padding: 40px 20px;
      }
    }
  }
}