.header{
  flex-shrink: 0;
  &__wrapper{
    height: 150px;
    padding: 30px 0;
    background: #232B3F;
  }
  &__content{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__block{
    &-btn{
      margin-left: auto;
    }
    &-nav{
      margin-left: auto;
    }
    &-profile{
      margin-left: 35px;
    }
  }
  &__btn{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 150px;
    height: 42px;
    border-radius: 4px;

    background-color: $accentColor;

    font-weight: 600;
    font-size: 18px;
    color:#fff;
  }

  &-profile{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__subscribe{
      position: relative;
      &-label{
        background: rgba(229, 19, 74, 0.2);
        border-radius: 20px;

        display: flex;
        align-items: center;
        justify-content: center;

        width: 185px;
        height: 35px;

        flex-shrink: 0;

        font-weight: 600;
        font-size: 16px;
        color: #E5134A;

        position: relative;
      }
      &-term{
        font-weight: 500;
        font-size: 18px;

        color: #232B3F;

        background: #FFFFFF;
        box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
        border-radius: 4px;

        display: flex;
        align-items: center;
        justify-content: center;

        width: 150px;
        height: 50px;

        position: absolute;

        top: -50px;

        left: calc(50% - 75px);

        &::after{
          content: '';
          position: absolute;
          bottom: -8px;
          left: 15px;

          width: 0;
          height: 0;
          border-left: 9px solid transparent;
          border-right: 9px solid transparent;

          border-top: 9px solid #fff;
        }
      }
    }
    &__nav{
      position: absolute;
      top: 45px;
      right: 0;
      width: 270px;

      background: #EBEFF6;
      box-shadow: 0px 12px 24px 6px rgba(0, 0, 0, 0.08);
      border-radius: 20px;

      padding: 10px;
    }
    &__info{
      position: relative;
    }
    &__img{
      display: flex;
      align-items: center;

      margin-left: 15px;
      &-icon{
        width: 24px;
        height: 24px;

        margin-left: 5px;
        svg{
          width: 100%;
          height: 100%;
        }
      }
      &-item{
        width: 40px;
        height: 40px;

        border-radius: 50%;
      }
    }
  }
}

.profile {
  &-nav {
    &__item {
      &_mobile {
        display: none;
      }
    }

    &__link {
      display: flex;
      align-items: center;

      padding: 15px;

      &-icon {
        width: 24px;
        height: 24px;

        svg {
          width: 100%;
          height: 100%;
        }
      }

      &-label {
        margin-left: 10px;

        font-weight: 700;
        font-size: 18px;
        color: #232B3F;
      }
    }
  }
  &-term{
    margin-bottom: 10px;

    display: none;
    &__wrapper{
      width: 100%;
      height: 35px;

      display: flex;
      align-items: center;
      justify-content: center;


      background: rgba(229, 19, 74, 0.2);
      border-radius: 20px;
    }
    &__text{
      font-weight: 600;
      font-size: 16px;
      color: #E5134A;
    }
  }
}


@media screen and (max-width: 1140px ) {
  .header{
    &__block{
      &-nav{
        display: none;
      }
      &-profile{
        margin-left: auto;
      }
    }
  }
  .profile{
    &-nav{
      &__item{
        &_mobile{
          display: block;
        }
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .header{
    &-profile{
      &__subscribe{
        display: none;
      }
    }
  }
  .profile{
    &-term{
      display: flex;
    }
  }
}